<script>
import router from "@/router/router";
export default {
  name: "contentBox",
  data() {
    return {
      color: 0,
      isFormOpen: false,
      contents: {
        title: "",
        context: "",
        color: "",
        mood: this.$store.state.todaysMood.contents.mood,
        date: "",
      },
      moodTable: {
        1: "üòê",
        2: "üòÅ",
        3: "üòÇ",
        4: "üòî",
        5: "üò®",
        6: "üò°",
      },
      title: "",
      context: "",
      savedContents: [],
      componentKey: 0,
    };
  },

  components: {},
  mounted() {
    this.loadContents();
  },

  methods: {
    getColor() {
      const colors = `hsl(${parseInt(Math.random() * 24, 10) * 15}, 50%, 57%)`;
      this.color = colors;
    },
    createNewForm() {
      this.context = "";
      this.isFormOpen = true;
    },

    saveContents() {
      this.contents.color = this.color;
      this.contents.context = this.context;
      this.contents.mood = this.$store.state.todaysMood.contents.mood;
      this.contents.date = this.$store.state.todaysMood.contents.date;
      this.contents.title = this.title;
      this.isFormOpen = false;

      this.$axios
        .post("/todaysMood/save", this.contents)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    loadContents() {
      this.$axios
        .get("/todaysMood/list")
        .then((res) => {
          this.savedContents = res.data;
        })
        .catch(function (error) {
          alert(error);
        });
    },
    init() {
      this.$store.commit("initToDoList");
    },
  },
};
</script>

