const todaysMood = {
  state: {
    contents: {
      context: "",
      color: "",
      mood: 1,
      date: "",
    },
  },
  mutations: {
    saveDate(state, payload) {
      state.contents.date = payload;
    },
    saveMood(state, payload) {
      state.contents.mood = payload;
    },
    initInformations(state) {
      state.contents = {
        context: "",
        color: "",
        mood: 1,
        date: new Date(),
        isMoodAndDateSelected: false,
      };
      const day = state.contents.date.getDate().toString().padStart(2, "0");
      const year = state.contents.date.getFullYear().toString();
      const month = (state.contents.date.getMonth() + 1)
        .toString()
        .padStart(2, "0");
      state.contents.date = year + "-" + month + "-" + day;
    },
  },
};

export default todaysMood;
