<template>
  <div class="leftAnimationControllBox" @click="movePrev()" v-if="level != 1">
    <button class="arrow arrow-left"></button>
  </div>
  <div class="rightAnimationControllBox" @click="moveNext()" v-if="level != 3">
    <button class="arrow arrow-right"></button>
  </div>

  <calenderView v-if="this.level == 1" :class="{ moveRightToLeft: isLeft }" />
  <mood
    style="display: inline-block"
    v-if="this.level == 2"
    :class="{ moveRightToLeft: isLeft, moveLeftToRight: isRight }"
  />
  <mainPage
    v-if="this.level == 3"
    :key="componentKey"
    :class="{ mainPageMoveRight: isRight }"
    @update="reRender"
  />
</template>

<script>
import mood from "./components/ShowMoods.vue";
import calenderView from "./components/CalenderView.vue";
import mainPage from "./components/mainPage.vue";
export default {
  name: "App",
  components: {
    mood,
    calenderView,
    mainPage,
  },
  data() {
    return {
      level: 1,
      isLeft: false,
      isRight: false,
      componentKey: 0,
    };
  },
  mounted() {
    this.$store.commit("initInformations");
  },
  methods: {
    reRender() {
      this.componentKey += 1;
      console.log(this.componentKey);
    },
    movePrev() {
      this.isRight = true;
      setTimeout(() => {
        this.level -= 1;
        this.isRight = false;
      }, 250);
    },
    moveNext() {
      this.isLeft = true;
      setTimeout(() => {
        this.level += 1;

        this.isLeft = false;
      }, 250);
    },
  },
};
</script>

<style>
