  position: absolute;
  width: 100px;
  height: 100vh;

  top: 50%;
  transform: translate(0, -50%);

  left: 0%;
}
.rightAnimationControllBox {
  position: absolute;
  width: 100px;
  height: 100vh;

  top: 50%;
  transform: translate(0, -50%);
  right: 0%;
}
.leftAnimationControllBox:hover > .arrow-left {
  animation-duration: 0.25s;
  animation-name: leftMouseOver;
  animation-fill-mode: forwards;
}
.rightAnimationControllBox:hover > .arrow-right {
  animation-duration: 0.25s;
  animation-name: rightMouseOver;
  animation-fill-mode: forwards;
}

@keyframes leftMouseOver {
  from {
    margin-left: -10px;
  }
  to {
    margin-left: -50px;
  }
}
@keyframes rightMouseOver {
  from {
  }
  to {
    margin-right: -50px;
  }
}

@keyframes movingRightToLeftAnimation {
  from {
    transform: translate(-50%, -50%);
  }
  to {
    transform: translate(-1000%, -50%);
  }
}

@keyframes movingLeftToRightAnimation {
  from {
    transform: translate(-50%, -50%);
  }
  to {
    transform: translate(1000%, -50%);
  }
}

@keyframes mainPageMovingRight {
  from {
  }
  to {
    transform: translateX(1000%);
  }
}
.arrow-left {
  border-top: 50px solid transparent;
  border-bottom: 50px solid transparent;
  border-right: 50px solid black; /* ÌôîÏÇ¥Ìëú */
  float: left;
}

.arrow-right {
  border-top: 50px solid transparent;
  border-bottom: 50px solid transparent;
  border-left: 50px solid black; /* ÌôîÏÇ¥Ìëú */
  float: right;
}

body {
  -ms-overflow-style: none;
}
body::-webkit-scrollbar {
  display: none;
}
.moveRightToLeft {
  animation-duration: 0.5s;
  animation-name: movingRightToLeftAnimation;
}
.moveLeftToRight {
  animation-duration: 0.5s;
  animation-name: movingLeftToRightAnimation;
}

.mainPageMoveRight {
  animation-duration: 0.5s;
  animation-name: mainPageMovingRight;
}
