const toDoList = {
  state: {
    toDoList: [],
  },
  mutations: {
    saveContext: function (state, payload) {
      const idx = state.toDoList.length;
      state.toDoList.unshift({
        index: idx,
        context: payload.context,
        id: payload.id,
        color: payload.color,
      });
    },
    initToDoList: function (state) {
      state.toDoList = [];
      state.context = [];
    },
  },
};

export default toDoList;
