<template>
  <div class="main">
    <div
      v-if="fullDate in this.contents"
      class="calenderTitleBox"
      :style="{ background: this.contents[fullDate].color }"
    >
      <a class="titleContext">{{ this.contents[fullDate].title }}</a>
    </div>
    <div
      class="contentBox"
      v-if="fullDate in this.contents"
      :style="{ background: this.contents[fullDate].color }"
    >
      <button class="delete" @click="deleteContent(this.fullDate)">X</button>
      <button class="correction">V</button>
      <a class="context">{{ this.contents[fullDate].context }}</a>
    </div>
    <a class="title" v-else>Ïò§ÎäòÏùò Í∏∞Î∂ÑÏùÑ ÏïåÎ†§Ï£ºÏÑ∏Ïöî!</a>
    <div class="calendar">
      <div class="header">
        <button class="calenderArrow" @click="prevMonth">&lt;</button>
        <span>{{ currentMonth }}</span>
        <button class="calenderArrow" @click="nextMonth">&gt;</button>
      </div>
      <div class="days">
        <div
          v-for="day in days"
          :key="day"
          class="day"
          @click="handleDateClick(day)"
          :class="{ selected: selectedDay === day }"
        >
          {{ day }}
          <br />
          <div v-for="writedDay in this.writedMoods" :key="writedDay">
            <a
              style="font-size: 30px"
              v-if="
                writedDay.year == this.currentDate.getFullYear().toString() &&
                writedDay.month ==
                  this.monthNames[this.currentDate.getMonth()] &&
                writedDay.day == day.toString().padStart(2, '0')
              "
              >{{ this.moodTable[writedDay.mood] }}</a
            >
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import router from "@/router/router";
export default {
  data() {
    return {
      currentDate: new Date(),
      selectedDate: new Date().getDate(), // ÏÑ†ÌÉùÎêú ÎÇ†ÏßúÎ•º Ï†ÄÏû•Ìï† Î≥ÄÏàò
      selectedDay: new Date().getDate(),
      info: {},
      moods: {},
      contents: {
        context: "",
        color: "",
        mood: this.$store.state.todaysMood.contents.mood,
        date: "",
      },
      writedMoods: [],
      monthNames: [
        "01",
        "02",
        "03",
        "04",
        "05",
        "06",
        "07",
        "08",
        "09",
        "10",
        "11",
        "12",
      ],
      moodTable: {
        1: "üòê",
        2: "üòÅ",
        3: "üòÇ",
        4: "üòî",
        5: "üò®",
        6: "üò°",
      },
    };
  },
  mounted() {
    this.$store.commit("saveDate", this.fullDate);

    this.$axios
      .get("/todaysMood/list")
      .then((res) => {
        this.info = res.data;
        for (var i = 0; i < this.info.length; i++) {
          this.moods = {};
          this.moods["year"] = this.info[i].date.substring(0, 4);
          this.moods["month"] = this.info[i].date.substring(5, 7);
          this.moods["day"] = this.info[i].date.substring(8, 10);
          this.moods["mood"] = this.info[i].mood;

          this.contents[this.info[i].date] = {
            context: this.info[i].context,
            color: this.info[i].color,
            title: this.info[i].title,
          };

          this.writedMoods.push(this.moods);
        }
      })
      .catch(function (error) {
        alert(error);
      });
  },
  computed: {
    currentMonth() {
      return (
        this.currentDate.getFullYear() +
        "ÎÖÑ  " +
        this.monthNames[this.currentDate.getMonth()] +
        "Ïõî"
      );
    },
    days() {
      const daysInMonth = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        0
      ).getDate();
      return Array.from({ length: daysInMonth }, (_, i) => i + 1);
    },
    fullDate() {
      return (
        this.currentDate.getFullYear().toString() +
        "-" +
        this.monthNames[this.currentDate.getMonth()] +
        "-" +
        this.selectedDay.toString().padStart(2, "0")
      );
    },
  },

  methods: {
    correctContent(info) {
      this.$axios
        .post("/todaysMood/correction", info)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    deleteContent(date) {
      this.$axios
        .post("/todaysMood/delete", date)
        .then((res) => {
          alert(res.data);
          router.go();
        })
        .catch(function (error) {
          alert(error);
        });
    },
    loadContents() {},
    prevMonth() {
      this.currentDate = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() - 1,
        1
      );
    },
    nextMonth() {
      this.currentDate = new Date(
        this.currentDate.getFullYear(),
        this.currentDate.getMonth() + 1,
        1
      );
    },
    handleDateClick(day) {
      this.selectedDay = day;
      this.$store.commit("saveDate", this.fullDate);
    },
  },
};
</script>

